type TextInputProps = {
    onBlur: (e:any) => void
}

function TextInput(props:TextInputProps){
    const [input, setInput] = useState('');
    function handleChange(e:any){
        console.log(e);
        setInput(e.target.value);
    }
    return(
        <Form.Control type='input' value={input} onChange={handleChange} onBlur={props.onBlur}/>
    );
}

type User = {
    type:string;
    id:number;
    name:string|undefined; 
    active: boolean;
    expires: number;
}

//const queryClient = new QueryClient();

function MainPage(){
    //const res = useServerQueryRequest(['user'], '/connect', {user: 1});
    //console.log(queryClient.getQueryData(['repoData']));
    console.log('mainRender');
    //const [cookie, setCookie] = useCookies(['admin']);
    const [userCookie, setUserCookie] = useCookies(['user']);
    //const sr = useServerRequest(handleReq);
    const [pageState, setPageState] = useState(0);
    const [guests, setGuests] = useState<any[] | null>(null);
    const [user, setUser] = useState<User | null>(null);
    //const refreshInterval = setInterval(() => sr.getServerRequest('', {}), 1000);
    /*const [pageUsers, setPageUsers] = useState<PageUsers>({
        userId: null,
        userCount: 0
    });*/
    const [pageAnalytics, setPageAnalytics] = useState<PageAnalytics>({
        autoRefresh: true,
        server: {
            ping: {total: 0, in: 0, out: 0}, status: 0
        }
    });
    const serverConnect = useServerPostQueryRequest(['connect'], '/connect', userCookie.user, null, Infinity);
    const serverUserBody = {user: user};
    const eventCount = useTimedEvent({time: 1000, function: handleRefreshServer, paused: !pageAnalytics.autoRefresh, variables: serverUserBody});

    useEffect(() => {
        if(serverConnect.status === 1 && !user){
            console.log(serverConnect.data);
            setUser(serverConnect.data.user);
            if(!guests){
                setGuests(serverConnect.data.guests);
            }
            if(!pageAnalytics.server.status) handleReq(serverConnect.data.serverTimes, 1);
        }
        return () => {
            //testDisconnect();
        }
    }, [serverConnect]);
    useEffect(() => {
        function disconnectGuest() {
            if (document.visibilityState === 'hidden') {
                console.log('hidden');
                const blob = new Blob([JSON.stringify(serverUserBody)], {type: 'application/json'});
                navigator.sendBeacon('http://localhost:3001/disconnect', blob);
            }
        }
        window.addEventListener('unload', disconnectGuest);
        return () => {
            window.removeEventListener('unload', disconnectGuest);
        };
    }, [serverUserBody]);
    async function handleRefreshServer(variables:any){
        //console.log(variables);
        const check = await postServerQueryRequest(['check'], '/check', variables);
        if(check.error){
            console.log('error on refresh');
        }else{
            if(check.query?.data){
                console.log(check.query.data);
                handleReq(check.query.data.serverTimes, 1);
                setGuests(check.query.data.guests);
                if(check.query.data.user) setUser(check.query.data.user);
            }
        }
    }
    function refreshAnalytics(){
        handleRefreshServer(serverUserBody);
    }
    async function testDisconnect(){
        if(serverConnect.status === 1){
            console.log('disconnect');
            console.log(serverConnect.data.user);
            const discon = await postServerQueryRequest(['disconnect'], '/disconnect', serverUserBody);
            if(discon.query) console.log(discon.query.data);
            
        }
    }
    function handleReq(serverTimes: any, code?:number){
        //console.log(code);
        code = code ? code : 1;
        const now = Date.now();
        const fullPing = now - serverTimes.incoming;
        const out = now - serverTimes.outgoing;
        setPageAnalytics(update(pageAnalytics, {
            server: {
                $set: {
                    ping: {total: fullPing, in: serverTimes.ping_in, out: out}, 
                    status: code
                }
            }
        }));
    }   
    
    function handleAppClick(i:number){
        return function(){
            setPageState(i);
        }
    }
    function handleAutoRefresh(){
        setPageAnalytics(update(pageAnalytics, {
            autoRefresh: {$set: !pageAnalytics.autoRefresh}
        }));
    }
    async function handleInputName(e:any){
        console.log(e);
        if(user){
            const newUser:User = {...user};
            newUser.name = e.target.value;
            setUser(newUser);
            const rename = await postServerQueryRequest(['renameuser'], '/renameuser', {user: newUser});
            if(!rename.error){
                console.log(rename.query);
                //setUser(rename.query?.data.user);
            }
        }

    }
    return <>
        <Button onClick={refreshAnalytics}>Test</Button>
        {serverConnect.data && <>
        <div>{user && (user.name ? user.name : user.type+user.id)}</div>
        <div>
            Guests Here:
            {guests && guests.map((guest:any, i:number) => {
                return <div key={i}>{guest.name ? guest.name : 'guest'+guest.id}</div>;
            })}
        </div>
        </>
        }
        {<RenderServerAnalytics {...pageAnalytics.server} onRefreshServer={refreshAnalytics}/>}
        <Button onClick={handleAutoRefresh} variant={pageAnalytics.autoRefresh ? 'danger' : 'primary'}>
            {pageAnalytics.autoRefresh ? 'Turn Auto Refresh Off' : 'Turn Auto Refresh On'}
        </Button>
        <TextInput onBlur={handleInputName}/>
        {applets[pageState].render()}
        <Styles.Flex flexDirection='row'>
        {applets.map((app, i) => {
            return <AppCard key={i} title={app.name} text={''} onClick={handleAppClick(i)}/>
        })}
        </Styles.Flex>
    </>
}

function RenderServerAnalytics(props:ServerAnalyticsProps){
    return (
        <Styles.Flex>
            <div>
            Server Status  
            <Styles.Dot onClick={props.onRefreshServer} radius={20} colour={serverStatus[props.status].colour}/>
            </div>
            <div>
                <span>Ping:</span><span>{props.ping.total}</span>
            </div>
            <div>
                <span>In:</span><span>{props.ping.in}</span>
            </div>
            <div>
                <span>Out:</span><span>{props.ping.out}</span>
            </div>
        <div/>
        </Styles.Flex>
    );
}

function AppCard(props:AppCardProps){
    return (
    <Card style={{ width: '18rem', cursor: 'pointer' }} onClick={props.onClick}>
        <Card.Body>
        <Card.Title>{props.title}</Card.Title>
        <Card.Text>
            {props.text}
        </Card.Text>
        </Card.Body>
    </Card>
    );
}

class Boxes{
    boxes: boolean[];
    sel: number;
    constructor(){
       this.boxes = [true, false, false, false, false];
       this.sel = 0;
    }
    changeBox(i:number){
        this.boxes[this.sel] = false;
        this.sel = i;
        this.boxes[i] = true;
    }
    draw(cr:CanvasRenderingContext2D){
        cr.fillStyle = 'red';
        for(let j=0; j < 5; j++){
            if(this.boxes[j]) cr.fillRect(j*50, 0, 50, 50);
        }
    }
}

type Position2D = {
    x:number;
    y:number;
}

class DrawGrid{
    grid: number[][];
    elements: Position2D[];
    squareSize: number;
    x: number; y: number;
    colour:Colour;
    constructor(){
        this.grid = [];
        this.squareSize = 2;
        this.elements = [];
        this.x = 0; this.y = 0;
        this.colour = new Colour(0,0,0);
    }

    draw(cr:CanvasRenderingContext2D){
        //cr.fillStyle = this.colour.toString();
        cr.fillStyle = '#FF55FF';
        this.elements.forEach((ele) => {
            //console.log(ele);
            cr.fillRect(this.x+(ele.x*this.squareSize), this.y+(ele.y*this.squareSize), this.squareSize, this.squareSize);
        });
    }
}

function C1(){
    console.log('render c1');
    const width = 1200; const height = 800;
    const renderer = useRef<CanvasRenderingContext2D | null>(null);
    const animTime = useAnim(renderer.current, animationStep);
    //const boxes = useRef<Boxes>(new Boxes());
    const grid = useRef<DrawGrid>(new DrawGrid());
    const connectServer = useRef(true);
    const animCirc = useMemo(() => {
        //const vc = new VirtCircle(5, 5, 5);
        //vc.setColour(new Colour(100, 200, 50));
        //return vc;
    }, []);
    const tickPeriod = 100;
    const tickTime = useRef(0);

    async function animationStep(cr:CanvasRenderingContext2D, time?:number){
        cr.clearRect(0, 0, width, height);
        grid.current.draw(cr);
        //cr.
        //boxes.current.draw(cr);
        //grid.current.draw(cr);
        //animCirc.fill(cr);
        //console.log('step');
        //animCirc.moveN(1,1);
        tickTime.current += animTime.current.frameTime;
        if(tickTime.current > tickPeriod){
            if(connectServer.current){
                /*
                getServerRequest('', {}, (res:any) => {
                    //console.log(res.data);
                    if(res.data.success){
                        const circ = res.data.circle;
                        //console.log(circ);
                        animCirc.place(new Point(circ.point.x, circ.point.y));
                    }
                });
                */
                const nextFrame = await getServerQueryRequest(['ot5'], '/sandsim');
                if(!nextFrame.error){
                    console.log(nextFrame.query?.data);
                    const data = nextFrame.query?.data;
                    //boxes.current.changeBox(nextFrame.query?.data.value);
                    grid.current.elements = data.grains;
                    grid.current.colour = new Colour(data.colours[0][0], data.colours[0][1], data.colours[0][2]);

                    cr.clearRect(0, 0, width, height);
                    grid.current.draw(cr);
                }
            }
            tickTime.current -= tickPeriod;
        }
    }
    function handleInit(cr:CanvasRenderingContext2D){
        renderer.current = cr;
    }
    function handleReset(){
        const s = getServerQueryRequest([], '/resetsim');
    }
    return <>
    App1
    <Button onClick={handleReset}>Reset</Button>
    {<Canvas onInit={handleInit} width={width} height={height}/>}
    {/*<Anim animationStep={animationStep} width={width} height={height}/>*/}
    </>
}

